import React from 'react';


import {Link, withRouter} from 'react-router-dom';



class Header extends React.Component
{
    constructor(props)
    {
        super(props);
     
        this.links = [{name:"Home",id:0,target:"/"},
        {name:"About",id:1,target:"/about"},
        
        {name:"Achievements",id:3,target:"/achievements"},
        {name:"Contact",id:4,target:"/contact"},

    
    ];


            for(let j=0;j<this.links.length;j++)
            {
             
                if(this.links[j].target == this.props.location.pathname)
                {
                    this.state = 
                    {
                        current: this.links[j].id
                    }
                }
            }
    


        
    }
    render()
    {
        let items = [];
        for(var i=0;i<this.links.length;i++)
        {
            let obj = this.links[i];
            items.push(
                <li className={(obj.id==this.state.current)?["active"]:""}>
                        <Link onClick={()=>{this.setState({current: obj.id});}}  to={obj.target}>{obj.name}</Link>
                </li>



            );
        }
        return (
<div className="navigation">
            
      


                            <ul className="navigation">     
                                {items}
                            </ul>

                          <hr />
                    
                            </div>              



        );

    }

}
export default withRouter(Header);
